import styled from 'styled-components';

const SkillsWrapper = styled.section`
  position: absolute;
  top: 10px;
  right: 5%;
  z-index: 10;
  width: 55%;
  padding: 1% 2% 1%;
  background-color: #fff;
`;

export default SkillsWrapper;



